
.user_header{
		position: relative;
		width: 100%;
		padding: 30px 0;
		border-bottom: 10px solid #f1f1f1;
}
.icon-setup{
		position: absolute;
		font-size: 22px;
		padding: 10px;
		color: #888;
		right: 10px;
		top: 10px;
}
.user_signin{
		text-align: center;
		font-size: 16px;
		line-height: 2;
		color: #666;
}
.user_signin img{
		width: 80px;
		height: 80px;
		border-radius: 50%;
}
.movie_tab{
    width:100%;
    height:40px;
}
.movie_tab ul{
    position: fixed;
    width:100%;
    height:40px;
    line-height:40px;
    overflow: hidden;
    background: #fff;
    border-bottom: 1px solid #f1f1f1;
    z-index: 2;
}
.movie_tab ul li{
    float: left;
    width:20%;
    text-align: center;
    font-size: 12px;
    color: #aaa;
}
.movie_tab ul li.on{
    color: #222;
    height:38px;
    border-bottom: 2px solid #474747;
}
.scan_item dl{
  	text-align: center;
  	font-size: 18px;
  	color: #888;
  	line-height: 4;
}



