
.movie_item{
    display: block;
		padding: 0 16px;
		margin:30px 0;
    overflow: hidden;
}
.rank_rating{
		position: relative;
	    margin: 30px auto;
	    text-align: center;
	    height: 10px;
	    width: 50%;
}
.rank_rating .line{
		position: relative;
}
.rank_rating .line:after{
		display: block;
	    position: absolute;
	    left: 0;
	    top: 4px;
	    border-bottom: 1px solid #ddd;
	    width: 200%;
	    content: "";
	    transform-origin: 0 0;
	    -webkit-transform-origin: 0 0;
	    transform: scale(.5);
	    -webkit-transform: scale(.5);
}
.rank_rating .text{
		padding: 0 10px;
	    display: inline-block;
	    background-color: #fff;
	    position: absolute;
	    left: 50%;
	    top: -8px;
	    -webkit-transform: translateX(-50%);
	    transform: translateX(-50%);
	    font-size: 18px;
	    color: #888;
}
.text.one{
		color: #d71d1d;
}
.text.two{
		color: #ee4e07;
}
.text.three{
		color: #eeaa07;
}
.rank_info{
		padding: 16px;
		border: 1px solid #eee;
		-webkit-box-shadow: 1px 1px 3px rgba(0,0,0,.05);
		        box-shadow: 1px 1px 3px rgba(0,0,0,.05)
}
.info_pic{
    float: left;
    width:80px;
}
.info_box{
    margin-left: 90px;
}
.info_box .name{
    font-size: 18px;
    color: #000;
}
.star{
    color: #ccc;
}
.star span{
    margin-left:3px;
    font-size: 13px;
}
.star .on{
    color: #feb10c;
}
.info_box .txt{
    margin-top: 5px;
    font-size: 12px;
    color: #888;
}



