
.movie_item{
	position: relative;
	width: 100%;
	padding: 16px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	border-bottom: 1px solid #f1f1f1;
	overflow: hidden;
}
.movie_item .pic{
	float: left;
	width:88px;
}
.movie_desc{
	margin-left: 100px;
}
.movie_desc p{
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}
.movie_desc .title{
	font-size: 16px;
	color: #474747;
}
.movie_desc .txt{
	font-size: 12px;
	color: #aaa;
}
.movie_desc .scan{
	font-size: 12px;
	color: #474747;
}
.get_sale{
	position: absolute;
	top: 50%;
	right: 26px;
	width: 50px;
	padding: 3px 0;
	text-align: center;
	color: #feb10c;
	font-size: 13px;
	border:1px solid #feb10c;
	border-radius: 4px;
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
}
.star{
	font-size: 10px;
	color: #ccc;
}
.star span{
	margin-left:3px;
	font-size: 13px;
}
.star .on{
	color: #feb10c;
}
.no_num{
	font-size: 12px;
	color: #aaa;
	line-height: 2.4;
}
.profess{
	margin-top: 10px;
	font-size: 12px;
	color: #888;
	overflow: hidden;
}
.profess span{
	float: left;
	margin-right: 10px;
	padding: 2px 5px ;
	background: #f4f4f4;
}



