
.rank_title{
		font-size: 18px;
		color: #474747;
		line-height: 2;
}
.rank_box{
		padding: 20px;
}
.rank_item{
		margin-top: 15px;
	    height: 130px;
	    border-radius: 10px;
	    color: #fff;
}
.top250{
		background: -webkit-gradient(linear,left top, left bottom,from(#e1a708),to(#efd491));
		background: linear-gradient(#e1a708,#efd491);
}
.weekly{
		background: -webkit-gradient(linear,left top, left bottom,from(#32c05e),to(#a1e5be));
		background: linear-gradient(#32c05e,#a1e5be);
}
.new_movie{
		background: -webkit-gradient(linear,left top, left bottom,from(#a361c3),to(#d9b9e8));
		background: linear-gradient(#a361c3,#d9b9e8);
}
.us_box{
		background: -webkit-gradient(linear,left top, left bottom,from(#dd7286),to(#f3b9c5));
		background: linear-gradient(#dd7286,#f3b9c5);
}
.rank_item .desc{
		vertical-align: top;
	    display: inline-block;
	    -webkit-box-sizing: border-box;
	            box-sizing: border-box;
	    padding-top: 50px;
	    width: 40%;
	    height: 100%;
    font-size: 12px;
	    text-align: center;
}
.rank_item .desc .name{
		font-size: 18px;
}
.rank_item .rank_img{
		  vertical-align: top;
	    display: inline-block;
	    width: 55%;
      max-width:210px;
	    height: 100%;
	    text-align: center;
}
.rank_img img{
		display: inline-block;
	    vertical-align: text-top;
	    position: relative;
	    top: 50%;
	    -webkit-transform: translateY(-50%);
	    transform: translateY(-50%);
	    width: 28%;
}
.rank_img img:nth-child(2){
		width: 35%;
}



