
.tab_box{
	width: 100%;
}
.tab_box ul{
/*position: fixed;*/
	width: 100%;
	height: 50px;
	line-height: 50px;
	border-bottom: 1px solid #f1f1f1;
	background: #fff;
	z-index: 2;
}
.tab_box ul li{
	float: left;
	width: 50%;
	text-align: center;
	color: #888;
}
.tab_box ul li.active{
	color: #474747;
	border-bottom: 2px solid #666;
}
.swiper-top{
	padding-top: 20px;
}
.swiper-slide{
	font-size: 12px;
	line-height: 2;
	text-align: center;
	color: #888;
}
.detail_pic{
	width: 100%;
	text-align: center;
	background: #4f4f4f;
}
.detail_pic img{
	margin:10px auto 30px;
	width: 160px;
}
.detail_top{
	overflow: hidden;
	padding: 16px ;
	border-bottom: 1px solid #f1f1f1;
}
.detail_title{
	margin-right: 94px;
	font-size: 18px;
	color: #333;
}
.detail_txt{
	margin-top: 8px;
	font-size: 12px;
	color: #aaa;
}
.star{
	font-size: 10px;
	color: #ccc;
}
.star .on{
	color: #feb10c;
}
.detail_grade{
	float: right;
	padding: 4px;
	width: 86px;
	height: 86px;
	text-align: center;
	font-size: 12px;
	color: #aaa;
	-webkit-box-shadow: 2px 2px 5px rgba(0,0,0,.2);
	        box-shadow: 2px 2px 5px rgba(0,0,0,.2)
}
.detail_grade .grade_num{
	font-size: 16px;
	color: #474747;
}
.detai_content{
	padding: 20px 16px;
}
.detail_h{
	font-size: 13px;
	color: #888;
}
.detail_desc{
	margin-top: 10px;
	font-size: 14px;
	color: #666;
}
.open_all{
	color: #15ef0a
}



