
.movie_tab{
  width:100%;
  height:40px;
}
.movie_tab ul{
  position: fixed;
  width:100%;
  height:40px;
  line-height:40px;
  overflow: hidden;
  background: #fff;
  border-bottom: 1px solid #f1f1f1;
  z-index: 2;
}
.movie_tab ul li{
  float: left;
  width:20%;
  text-align: center;
  font-size: 12px;
  color: #aaa;
}
.movie_tab ul li.on{
  color: #222;
  height:38px;
  border-bottom: 2px solid #474747;
}
.loading{
  margin-top: 60px;
  width:100%;
  text-align: center;
  font-size: 16px;
  color: #888;
  line-height:3px;
}



