
.discuss_item{
	padding:10px 16px;
}
.discuss_title{
	font-size: 16px;
	color: #474747;
}
.discuss_author{
	margin:5px 0;
	font-size: 12px;
	height: 24px;
	line-height: 24px;
	color: #aaa;
	overflow: hidden;
}
.discuss_author span,
.discuss_author .star{
	display: inline-block;
}
.discuss_summary{
	font-size: 12px;
	color: #999;
}
.more_info{
	margin-top: 5px;
	font-size: 12px;
	color: #999;
}
.star{
	font-size: 10px;
	color: #ccc;
}
.star .on{
	color: #feb10c;
}



