
.select .iconfont,
.select span{
	color: #feb10c
}
.comment_item{
	padding: 10px 16px;
	overflow: hidden;
}
.comment_item .comment_user{
	float: left;
}
.comment_user img{
	width: 30px;
	height: 30px;
	border-radius: 50%;
}
.comment_content{
	margin-left: 40px;
}
.comment_author{
	height: 24px;
	line-height: 24px;
	overflow: hidden;
}
.comment_author .name{
	float: left;
	max-width: 40%;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	font-size: 14px;
	color: #333;
}
.comment_author .star{
	float: left;
	margin-left:10px;
}
.comment_author .zan{
	float: right;
	color: #aaa;
	font-size: 12px;
}
.star .iconfont{
	color: #ddd;
}
.star .iconfont.on{
	color: #feb10c;
}
.comment_txt{
	margin:5px 0;
	font-size: 14px;
	color: #666;
}
.comment_time{
	font-size: 12px;
	color: #aaa;
}
.select{
	color: #feb10c
}



